.title {
  font-family: Helvetica, sans-serif;
  color: #d0624d;
  border-bottom: 1px dotted #a0978d;
}

.container {
    background-color: #e8dfce;
    width: 800px;
    padding-top: 20px;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 80px;
    margin: auto;
    box-shadow: 6px 6px 30px black, inset 20px 0px 20px -20px #000000, inset -20px 0px 20px -20px #000000;
}

.book {
  display: flex;
  justify-content: center;
}

.ear {
  
  text-align: center;
  width: 72px;
  height: 72px;
  
  padding: 20px;
  margin: 30px;
}

.big {
  font-size: 84px;
}

.small {
  margin-left: 12px;
  margin-top: 32px;
  font-size: 48px;
  position: absolute;
}

.count {
  line-height: 72px;
  font-family: "Brush Script MT", cursive;

  border-radius: 50%;

  color: white;
  text-shadow: -1px -1px 0 rgb(255, 0, 0), 1px -1px 0 rgb(255, 0, 0),
    -1px 1px 0 rgb(255, 0, 0), 1px 1px 0 rgb(255, 0, 0);
  background-color: #d6c7b5;
}
