.button {
    font-size: 48px;
    font-family: "Brush Script MT", cursive;
    font-weight: bold;
    border-width: 0px;
    position: relative;
}

.button:hover:after {
    
}

.dark {
    background-color: #efd79d;
    color: #eff2ae;
}

.light {
    background-color: #eff2ae;
    color: #e7cb8e;
}

.button:after {
    position: absolute;
    width: 60px;
    height: 60px;
    top: 4px;
    left: 4px;
    content: '';
    border-radius: 50%;
}

.active:after {
    border: 6px solid rgba(231, 61, 9, 0.747);;
}

.active:hover:after {
    border: 6px solid rgba(167, 157, 155, 0.479);
}

.inactive {
}

.inactive:hover:after {
    border: 6px solid rgba(247, 65, 10, 0.877); 
}